const SkillsContainer = styled.div`
  display: flex;
  flex-direction: column;
  /* border: 1px solid black;
    box-sizing: border-box; */
`

const SingleSkillContainer = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  /* border: 1px solid red;
    box-sizing: border-box; */
`


export default props => {
  const skills = {
    PhotoShop: 95,
    Illustration: 80,
    Indesign: 40,
    Coreldraw: 60,
    'Microsoft Word': 30,
    Freehand: 95
  }
  
  return (
    <SkillsContainer>
      {Object.keys(skills).map((skill, i) => 
        <SingleSkillContainer key={i}>
        <h3 style={{width: '50%', textAlign: 'left'}}>{skill}</h3>
        <Meter
            type='bar'
            size='xsmall'
            thickness='xsmall'
            background='light-4'
            values={[
              {
                // label: 'First',
                value: skills[skill],
                color: 'neutral-3'
                // highlight: (active === 'First'),
                // onHover: over => this.setState({ active: over ? 'First' : undefined }),
              },
              // {
              //   label: 'Second',
              //   value: 20,
              //   highlight: (active === 'Second'),
              //   onHover: over => this.setState({ active: over ? 'Second' : undefined }),
              // },
            ]}
          />
        </SingleSkillContainer>
        )}
    </SkillsContainer>
  )
}

